@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&display=swap');

/* Custom Base Styles */
body {
  font-family: 'Poppins', sans-serif;
  background-color: #f8f9fa;
  color: #333;
}

/* Custom Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.animate-fadeIn {
  animation: fadeIn 0.5s ease-in-out;
}

/* Glass Morphism Effect */
.glass {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

/* Gradient Buttons */
.gradient-btn {
  background: linear-gradient(90deg, #4f46e5 0%, #7c3aed 100%);
  transition: all 0.3s ease;
}
.gradient-btn:hover {
  background: linear-gradient(90deg, #7c3aed 0%, #4f46e5 100%);
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(124, 58, 237, 0.2);
}

/* Futuristic Animations */
@keyframes shimmer {
  100% { background-position: 200% center; }
}
.shimmer {
  background: linear-gradient(90deg, #00ffea 25%, #651fff 50%, #00ffea 75%);
  background-size: 200% 100%;
  animation: shimmer 2s linear infinite;
}

@keyframes glow {
  0%, 100% { box-shadow: 0 0 8px #00ffea, 0 0 16px #651fff; }
  50% { box-shadow: 0 0 24px #00ffea, 0 0 48px #651fff; }
}
.animate-glow {
  animation: glow 2s alternate infinite;
}

@keyframes pulse-neon {
  0%, 100% { box-shadow: 0 0 8px #00ffea; }
  50% { box-shadow: 0 0 32px #00ffea; }
}
.pulse-neon {
  animation: pulse-neon 1.5s infinite;
}